<template>
    <b-container fluid>
      <raw-loader :mytxt="require('!raw-loader!./Users.vue').default"></raw-loader>
      {{ items }}
      <form @submit.stop.prevent="onSave">
        <b-table ref="mytable" :items="items" :fields="fields" :current-page="currentPage" :per-page="perPage"
          :filter="filter" :filter-included-fields="filterOn" :sort-by.sync="sortBy" :sort-desc.sync="sortDesc"
          :sort-direction="sortDirection" stacked="md" show-empty small fixed outlined bordered @filtered="onFiltered">
  
          <template v-slot:cell()="{ value, item, field: { key } }">
            <template v-if="sel[pri] != item[pri]">
              <template v-if="item[pri] != 0">
                <template v-if="key == pri">
                  {{ value }}
                </template>
                <!-- <template v-if="key == 'name'">
                  {{ value }}
                </template> -->
                <template v-else >
                  {{ value }}
                </template>
              </template>
            </template>
            <template v-if="(sel[pri] == item[pri] || item[pri] == 0)">
              <template v-if="key == pri">
                <span>{{ value }}</span>
              </template>
              <template v-if="key == 'Username'">
                <b-form-input autocomplete="off" v-model="item[key]" :name="key" v-validate="'required'" placeholder=""
                  :class="{ 'ko-hop-le': errors.has(key) }">
                </b-form-input>
              </template>
              <template v-if="key == 'Password'">
                <b-form-input autocomplete="off" v-model="item[key]" :name="key" v-validate="'required'" placeholder=""
                  :class="{ 'ko-hop-le': errors.has(key) }">
                </b-form-input>
              </template>
              <template v-if="key == 'Roles'">
                <v-select multiple v-model="item[key]" v-validate="'required'" :name="key" :reduce="(option) => option.id"
                    :options="[
                    { label: 'Admin', id: 'Admin' },
                    { label: 'ChuQuan', id: 'ChuQuan' },
                    { label: 'NhanVien', id: 'NhanVien' },
                    { label: 'User', id: 'User' }
                    ]" :has-error="errors.has(key)" :class="{ danger: errors.has(key) }" />
              </template>


              <!-- <v-select multiple v-model="selected" :options="['Canada','United States']" /> -->

              
            </template>
          </template>
  
          <template #cell(cmd)="row">
            <template v-if="row.item[pri] == 0">
              <b-button variant="outline" class="mr-1" size="sm" @click="onSave(row.item)">
                <b-icon-hand-thumbs-up></b-icon-hand-thumbs-up>
              </b-button>
              <b-button variant="outline" class="mr-1" size="sm" @click="OnCancel(row.item)">
                <b-icon-file-x></b-icon-file-x>
              </b-button>
            </template>
            <template v-else>
              <b-button variant="outline" :disabled="isAdded() == true" class="mr-1" v-if="sel[pri] != row.item[pri]" size="sm"
                @click="OnEdit(row.item)">
                <b-icon-pencil-square></b-icon-pencil-square>
              </b-button>
              <b-button variant="outline" :disabled="isAdded() == true" class="mr-1" v-if="sel[pri] != row.item[pri]" size="sm"
                @click="OnDel(row.item)">
                <b-icon-trash></b-icon-trash>
              </b-button>
              <b-button variant="outline" class="mr-1" v-if="sel[pri] == row.item[pri]" size="sm" @click="onSave(row.item)">
                <b-icon-hand-thumbs-up></b-icon-hand-thumbs-up>
              </b-button>
              <b-button variant="outline" class="mr-1" v-if="sel[pri] == row.item[pri]" size="sm" @click="OnCancel(row.item)">
                <b-icon-file-x></b-icon-file-x>
              </b-button>
            </template>
          </template>
  
          <template #bottom-row="data">
            <td :colspan="data.columns">
              <b-row>
                <b-col sm="6" class="my-1">
                  <b-form-group label="Filter" label-for="filter-input" label-cols-sm="3" label-align-sm="right"
                    label-size="sm" class="mb-0">
                    <b-input-group size="sm">
                      <b-form-input id="filter-input" v-model="filter" type="search"
                        placeholder="Type to Search"></b-form-input>
                      <b-input-group-append>
                        <b-button :disabled="!filter" @click="filter = ''">Clear</b-button>
                      </b-input-group-append>
                    </b-input-group>
                  </b-form-group>
                </b-col>
                <b-col sm="6" class="my-1">
                  <b-form-group label="Per page" label-for="per-page-select" label-cols-sm="6" label-cols-md="4"
                    label-cols-lg="3" label-align-sm="right" label-size="sm" class="mb-0">
                    <b-form-select id="per-page-select" v-model="perPage" :options="pageOptions"
                      size="sm"></b-form-select>
                  </b-form-group>
                </b-col>
                <b-col sm="6" class="my-1 text-right">
                  page :{{ currentPage }} / {{ getLastPage(totalRows) }} of {{ totalRows }}
                  <b-button :disabled="adding == true || isEditing() == true" @click="OnAdd"
                    size="sm"><b>Add</b></b-button>
                </b-col>
                <b-col sm="6" class="my-1">
                  <b-pagination :disabled=" adding == true || isEditing() == true " v-model="currentPage" :total-rows="totalRows" :per-page="perPage" align="fill" size="sm"
                    class="my-0"></b-pagination>
                </b-col>
              </b-row>
  
            </td>
          </template>
  
        </b-table>
      </form>
      <!-- Info modal -->
      <b-modal :id="infoModal.id" :title="infoModal.title" ok-only @hide="resetInfoModal">
        <pre>{{ infoModal.content }}</pre>
      </b-modal>
    </b-container>
  </template>
  
  <script>
  
  import VeeValidate from "vee-validate";
  import Vue from "vue";
  Vue.use(VeeValidate, {
    classes: true,
    fieldsBagName: 'veeFields'
  });
  
  import moment from "moment";
  
  import vSelect from "vue-select";
  Vue.component("v-select", vSelect);
  //import "vue-select/dist/vue-select.css";
  
  export default {
    data() {
      return {
        items: [
        ],
        fields: [
          {
            key: 'Id',
            sortable: true,
            thClass: 'text-center',
            tdClass: 'text-center align-middle',
            //tdClass: 'align-middle'
            thStyle: { width: "8%" },
          },
          { key: 'Username', sortable: true, thClass: 'text-center', tdClass: 'align-middle', thStyle: { width: "20%" }},
          { key: 'Password', sortable: true, thClass: 'text-center', tdClass: 'align-middle', thStyle: { width: "10%" }},
          { key: 'Roles', sortable: true, thClass: 'text-center', tdClass: 'align-middle', thStyle: { width: "50%" }},
          {
            key: 'cmd',
            class: 'text-center',
            thClass: 'text-center',
            tdClass: 'align-middle',
            thStyle: { width: "12%" }
          }
        ],
        totalRows: 1,
        currentPage: 1,
        perPage: 3,
        pageOptions: [3,5, 10, 15, { value: 100, text: "Show a lot" }],
        sortBy: '',
        sortDesc: false,
        sortDirection: 'asc',
        filter: null,
        filterOn: [],
        infoModal: {
          id: 'info-modal',
          title: '',
          content: ''
        },
  
        sel: {},
        pri: 'Id',
        adding: false,
        lastPage:0,
  
      }
    },
    computed: {
      sortOptions() {
        // Create an options list from our fields
        return this.fields
          .filter(f => f.sortable)
          .map(f => {
            return { text: f.label, value: f.key }
          })
      }
    },
    mounted() {
      let items = [
      ];
      items.push(
        {Id:1,Username:'admin',Password:'123',Roles:["Admin","ChuQuan"]}
      )
    //   for(var i=1;i<6;i++){
    //     items.push({
    //       id:i,
    //       name:`thuc ${i}`
    //     })
    //   }
      this.items = items;
      this.totalRows = this.items.length;
      this.lastPage=this.getLastPage(this.totalRows);
    },
    methods: {
      createId(){
        let max=0;
        this.items.forEach(x=>{
          if(x[this.pri] > max){
            max=x[this.pri];
          }
        })
        return max+1;
      },
      onSave(item) {
        this.$validator.validateAll().then((result) => {
          if (!result) {
            this.toast("Không hợp lệ");
            return;
          }
          let r = JSON.parse(JSON.stringify(item));
          if(r[this.pri]==0){
            let index = this.items.findIndex(x => x[this.pri] == r[this.pri]);
            r[this.pri] = this.createId();//new Date().getTime();
            this.$set(this.items, index, r);
            this.adding=false;
            this.$refs.mytable.refresh();
          }else{
            let index = this.items.findIndex(x => x[this.pri] == r[this.pri]);
            this.$set(this.items, index, r);
            this.sel={};
          }
        });
      },
      OnDel(item) {
        this.delRow(item);
      },
      delRow(item) {
        let id = item[this.pri];
        let index = this.items.findIndex(x => x[this.pri] == id);
        this.items.splice(index, 1);
        this.totalRows = this.items.length;
        this.lastPage = this.getLastPage(this.totalRows);
      },
      doCancel(item) {
        for (let key in item) {
          if (item[key] != this.sel[key]) {
            item[key] = this.sel[key]
          }
        }
      },
      isAdded() {
        let kq = false;
        this.items.forEach(x => {
          if (x[this.pri] == 0) kq = true;
        })
        return kq;
      },
      OnEdit(item) {
        if (JSON.stringify(this.sel) != "{}") return;
        this.sel = JSON.parse(JSON.stringify(item));
      },
      OnCancel(item) {
        if (item[this.pri] == 0) {
          let index = this.items.findIndex(x => x[this.pri] == item[this.pri]);
          this.items.splice(index, 1);
          this.adding = false;
          this.$refs.mytable.refresh();
        } else {
          this.doCancel(item);
          this.sel = {};
        }
      },
      OnAdd() {
        // let r = {
        //   id: 0,
        //   name: 'Thức',
        // }
        let r={Id:0,Username:'sfsf',Password:'sfsf',Roles:'User'};
        this.items.unshift(r);
        this.adding = true;
        this.currentPage=1;
        this.totalRows=this.items.length;
        this.$refs.mytable.refresh();
      },
      isEditing() {
        return JSON.stringify(this.sel) != "{}"
      },
      getLastPage(count) {
        let pageSize = this.perPage;//this.tabulator.getPageSize();
        let du = count % pageSize;
        return du == 0 ? count / pageSize : (count - du) / pageSize + 1;
      },
      info(item, index, button) {
        this.infoModal.title = `Row index: ${index}`
        this.infoModal.content = JSON.stringify(item, null, 2)
        this.$root.$emit('bv::show::modal', this.infoModal.id, button)
      },
      resetInfoModal() {
        this.infoModal.title = ''
        this.infoModal.content = ''
      },
      onFiltered(filteredItems) {
        this.totalRows = filteredItems.length
        this.currentPage = 1
      },
      toast(msg, variant = null) {
        this.$bvToast.toast(msg, {
          title: `Information`,
          variant: variant,
          solid: true,
        });
      },
  
    }
  }
  </script>
